import { useSelector, useDispatch } from "react-redux"
import { changeLink, updateShowMenu } from '../store/actions/appAction'
import { NavLink } from "react-router-dom"

export const Page = ({ name, imgName }) => {
  const currLink = useSelector(({ appModule }) => appModule.currLink)
  const dispatch = useDispatch()
  const image = require(`../assets/img/links/${imgName}.png`).default
  const updateLink = () => {
    dispatch(updateShowMenu())
    dispatch(changeLink(name))
  }
  return (
    <NavLink activeClassName="active" className=" page flex" to={`${name}`} onClick={updateLink}>
      <img src={image} alt={name} />
      <span>{name}</span>
    </NavLink >
  )
}
