import { Doughnut } from 'react-chartjs-2';
const data = {
  labels: [
    'Websites',
    'Logo',
    'Social Media',
    'Adwords',
    'E-Commerce'
  ],
  datasets: [{
    data: [300, 50, 100, 300, 200],
    backgroundColor: [
      '#ba2f25',
      '#FF6384',
      '#5584ff',
      '#FFCE70',
      '#673ab7',
    ],
    hoverBackgroundColor: [
      '#FF6384',
      '#36A2EB',
      '#FFCE56'
    ]
  }]
};
export function Sales() {
  return (
    <div className="sales">
      <h1>Sales</h1>
      <Doughnut data={data} />
    </div>
  )
}
